import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/Home.vue'
import Form from '@/views/Form.vue'
import VueCharts from '@/views/charts/Vue-Charts/Chart.vue'
import HighCharts from '@/views/charts/Vue-Highcharts/Chart.vue'
import VueGraph from '@/views/charts/Vue-Graph/Chart.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/form',
    name: 'Form',
    component: Form
  },
  {
    path: '/vuecharts',
    name: 'vue-charts',
    component: VueCharts
  },
  {
    path: '/highcharts',
    name: 'highcharts',
    component: HighCharts
  },
  {
    path: '/vuegraph',
    name: 'vuegraph',
    component: VueGraph
  }
]

const router = new VueRouter({
  routes
})

export default router
