<template>
  <section class="charts">
    <graph-pie
      :width="500"
      :height="500"
      :padding-top="100"
      :padding-bottom="100"
      :padding-left="100"
      :padding-right="100"
      :values="values"
      :names="names"
      :active-index="[ 0, 2 ]"
      :active-event="'click'"
      :show-text-type="'outside'"
      :data-format="dataFormat"
    >
      <legends :names="names"></legends>
      <tooltip :names="names"></tooltip>
    </graph-pie>
  </section>
</template>
<script>
import GraphPie from "vue-graph/src/components/pie";

export default {
  data() {
    return {
      values: [10, 5, 5, 5],
      names: ["Apple", "Google", "MS", "Facebook"]
    };
  },
  methods: {
    dataFormat: function(a, b) {
      if (b) return b + "%";
      return a;
    }
  },
  components: {
    GraphPie
  }
};
</script>
