import { Bar } from 'vue-chartjs';
 
export default {
  extends: Bar,
  props: {
    labels: Array,
    data:Array
  },
  mounted () {
    this.renderChart({
      labels: this.labels,
      datasets: [
        {
          label: 'GitHub Commits',
          backgroundColor: '#f87979',
          data: this.data
        }
      ]
    }, {
      responsive: true,
      maintainAspectRatio: false
    });
  }
}